import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar } from '@ionic/react';
import './Ocaso.css';

import { useState, useEffect } from 'react';
import { SunriseResponse } from '../models/SunriseResponse';
import HourInTresArroyos from "../components/HourInTresArroyos";
import ServiceStatus from "../components/ServiceStatus";
import TresArroyosHeader from "../components/TresArroyosHeader";

const Ocaso: React.FC = () => {

  const [error, setError] = useState<{ message: string } | null>(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [sunriseResponse, setSunriseResponse] = useState<SunriseResponse | null>(null);

  useEffect(() => {
    fetch("https://api.sunrise-sunset.org/json?lat=-38.376878&lng=-60.275685&date=today&formatted=0")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setSunriseResponse(result);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])

  if (error) {
    return (
      <div>Error: { error?.message }</div>
    );
  } else if (!isLoaded) {
    return (
      <div>Loading...</div>
    );
  } else if (sunriseResponse){
    return (
      <IonPage>
        <TresArroyosHeader />
        <IonContent fullscreen>
          <IonHeader collapse="condense">
            <IonToolbar>
              <IonTitle size="large">Ocaso</IonTitle>
            </IonToolbar>
          </IonHeader>
          <div className="container">

            <img src="https://sunrise-sunset.org/sunset-100px.svg" alt="sunrise" width='100'/>
            <h1>Ocaso</h1>

            <h1>
              <HourInTresArroyos hour={sunriseResponse?.results?.sunset} />
            </h1>
            <ServiceStatus status={sunriseResponse.status === 'OK'}/>
            
          </div>
        </IonContent>
      </IonPage>
    );
  }else{
    return (
      <div>Loading...</div>
      );
  }
};

export default Ocaso;