
import { IonHeader, IonTitle, IonToolbar } from '@ionic/react';
import ChangeThemeButton from "../components/ChangeThemeButton";

const TresArroyosHeader: React.FC = () => {
  return (
    <IonHeader>
      <IonToolbar>
        <IonTitle>
          Tres Arroyos { new Date().toLocaleDateString() }
        </IonTitle>
        {/* <ChangeThemeButton /> */}
      </IonToolbar>
    </IonHeader>
  );
};

export default TresArroyosHeader;